/* Add shadows to bostrap cards */



.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}

/* Change the background color of cards on hover */
.card:hover {
  background-color: #ccc;
}

/* Add a card animation effect when its loaded */
/* jumping effect */
/* add delay to each card */

@keyframes jump {
  0% {
    transform: translateY(0);
    opacity: 0;
  }
  50% {
    transform: translateY(-10px);
    opacity: 0.5;

  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
/* add this to card class */
.card {
  animation: jump 0.5s;
  animation: fadein 3s;


}


/* navbar css */
/* make it pretty */
.navbar {
  border: 0;
  border-radius: 0;
  margin-bottom: 0;
  font-size: 12px !important;
  letter-spacing: 4px;
  opacity: 0.9;
}
.navbar li a, .navbar .navbar-brand {
  color: #d5d5d5 !important;
}
.navbar-nav button p li a:hover, .navbar-nav li.active a {
  color: #fff !important;
  background-color: #29292c !important;
}

/* add shadow to navbar */
.navbar-default {
  background-color: #29292c;
  border-color: #29292c;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}
/* add shadow to navbar on hover */
.navbar-default:hover {
  background-color: #29292c;
  border-color: #29292c;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

/* button click animation */
/* add this to boostrap button class */
.btn {
  animation: jump 0.4s;
}

/* add this to button class ON CLICK */
.btn:active {
  transform: translateY(4px);
  opacity: 0.5;
}

.App-footer {
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 50%;
  /* rounded corners for footer */
  border-radius: 0 0 10px 10px;
  /* add shadow to footer */
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  background-color: #ffffff;
  margin-bottom: 10px;
}

/* add shadow to footer on hover */
.App-footer:hover {
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 50%;
  /* rounded corners for footer */
  border-radius: 0 0 10px 10px;
  /* add shadow to footer */
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  background-color: #ffffff;
  margin-bottom: 10px;
}

.App-footer p {
  color: #000000;
}

/* fade in animation */
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* add this to the body */  
body {
  animation: fadein 0.7s;
}

.text-algin-left {
  text-align: left;
}